import req from '../../utils/req.js';
export default {
	data() {
		return {
			categorys:[],
			navIndex:0
		};
	},
	async mounted(){
		let result = await req('/getCategoryDatas');
		this.categorys = result
	},
	computed:{
		navList(){
			return this.categorys.map((item)=>{
				return item.name
			})
		},
		categoryObj(){
			return this.categorys[this.navIndex];
		}
	},
	methods:{
		changeNavIndex(index){
			this.navIndex = index;
		}
	}
}
